import axios from 'axios';

import { Config } from '../config/config';
axios.defaults.baseURL = Config.api_url;

export const sendMessage = async (message) => {
    let retVal = {
        success: -1,    //  -1: Error code, 0: failure, 1: success
        title: "",
        content: ""
    };
    return axios.post("/sendMessage", message)
        .then(res => {
            if(res.data.success) {
                retVal.success = 1;
                retVal.title = 'SUCCESS';
                retVal.content = 'REGISTER_SUCCESS';
                retVal.data = res.data;
                return retVal;
            } else {
                retVal.success = 0;
                retVal.title = 'WARNING';
                retVal.content = JSON.stringify(res.data.errMessage);
                retVal.data = null;
                return retVal;
            }
        })
        .catch( err => {
            retVal.success = -1;
            retVal.title = 'WARNING';
            retVal.content = 'ERR_NETWORK_CONNECTION';
            retVal.data = null;
            return retVal;
        })
}



