import React, { Component } from 'react';
import axios from 'axios';

class DownloadLink extends Component {
   render() {
      return (
         <a href={this.props.src} download>{this.props.children}</a>
      )
   }
}

class About extends Component {

   render() {

      if (this.props.data) {
         var name = this.props.data.name;
         var profilepic = "images/" + this.props.data.image;
         var bio = this.props.data.bio;
         var state = this.props.data.address.state;
         var country = this.props.data.address.country;
         var phone = this.props.data.phone;
         var email = this.props.data.email;
         var skype = this.props.data.skype;
         var skype_name = this.props.data.skype_name;
         var resumeDownload = this.props.data.resumedownload;
      }

      return (
         <section id="about">
            <div className="row">
               <div className="three columns">
                  <img className="profile-pic" src={profilepic} alt="Jerry Piller" />
               </div>
               <div className="nine columns main-col">
                  <h2>About Me</h2>

                  <p>{bio}</p>
                  <div className="row">
                     <div className="columns contact-details">
                        <h2>Contact Details</h2>
                        <p className="address">
                           <span>{name}</span><br />
                           {/* <span>{state}, {country}</span><br /> */}
                           <span>{phone}</span><br />
                           <span>{email}</span><br />
                           {/* <span>{skype}</span><br /> */}
                           <span>{skype_name}</span><br />
                        </p>
                     </div>
                     <div className="columns download">
                        <p>
                           <a href={resumeDownload} target="_blank" className="button"><i className="fa fa-download"></i>Download Resume</a>
                        </p>
                     </div>
                  </div>
               </div>
            </div>

         </section>
      );
   }
}

export default About;



