import React, { Component } from 'react';
import * as actions from "../actions";

class Contact extends Component {

   constructor(props) {
      super(props);

      this.state = {
        submitting: false,
        contactName: "",
        contactEmail: "",
        contactSubject: "",
        contactMessage: "",
      }
   }

   handleChange = e => {
      this.setState({
         [e.target.name]: e.target.value
     })
   }

   submit = async () => {
      if(this.state.contactName === "") {
         alert("Please check your name!");
         return;
      }
      if(this.state.contactEmail === "") {
         alert("Please check your email!");
         return;
      }
      if(this.state.contactSubject === "") {
         alert("Please check your subject!");
         return;
      }
      if(this.state.contactMessage === "") {
         alert("Please check your message!");
         return;
      }

      this.setState({
         submitting: true
      })

      let mailObj = {
         name: this.state.contactName,
         email: this.state.contactEmail,
         subject: this.state.contactSubject,
         message: this.state.contactMessage,
      }

      let retVal = await actions.sendMessage(mailObj);
      switch(retVal.success) {
         case -1:
            alert(retVal.content);
            break;
         case 0:
            alert(retVal.content);
            // this.setState({
            //    contactName: "",
            //    contactEmail: "",
            //    contactSubject: "",
            //    contactMessage: "",
            // })
            // alert("Your message was sent, thank you!");
            break;
         case 1:
            this.setState({
               contactName: "",
               contactEmail: "",
               contactSubject: "",
               contactMessage: "",
            })
            alert("Your message was sent, thank you!");
            break;
      }

      this.setState({
         submitting: false
      })

   }

  render() {

    if(this.props.data){
      var name = this.props.data.name;
      var state = this.props.data.address.state;
      var country = this.props.data.address.country;
      var email = this.props.data.email;
      var message = this.props.data.contactmessage;
    }

    return (
      <section id="contact">

         <div className="row section-head">

            <div className="two columns header-col">

               <h1><span>Get In Touch.</span></h1>

            </div>

            <div className="ten columns">

                  <p className="lead">{message}</p>

            </div>

         </div>

         <div className="row">
            <div className="eight columns">

               {/* <form action="" method="post" id="contactForm" name="contactForm"> */}
					<fieldset>

                  <div>
						   <label htmlFor="contactName">Name <span className="required">*</span></label>
						   <input type="text" defaultValue="" size="35" id="contactName" name="contactName" value={this.state.contactName} onChange={this.handleChange}/>
                  </div>

                  <div>
						   <label htmlFor="contactEmail">Email <span className="required">*</span></label>
						   <input type="text" defaultValue="" size="35" id="contactEmail" name="contactEmail" value={this.state.contactEmail} onChange={this.handleChange}/>
                  </div>

                  <div>
						   <label htmlFor="contactSubject">Subject</label>
						   <input type="text" defaultValue="" size="35" id="contactSubject" name="contactSubject" value={this.state.contactSubject} onChange={this.handleChange}/>
                  </div>

                  <div>
                     <label htmlFor="contactMessage">Message <span className="required">*</span></label>
                     <textarea cols="50" rows="15" id="contactMessage" name="contactMessage" value={this.state.contactMessage} onChange={this.handleChange}></textarea>
                  </div>

                  <div>
                     <button className="submit" onClick={this.submit} disabled={this.state.submitting}>
                        Submit 
                        <img alt="" src="images/loader.gif" style={{height: "15px"}} hidden={!this.state.submitting}/>
                     </button>
                     {/* <span id="image-loader">
                        <img alt="" src="images/loader.gif" />
                     </span> */}
                  </div>
					</fieldset>
				   {/* </form> */}

           <div id="message-warning"> Error boy</div>
				   <div id="message-success">
                  <i className="fa fa-check"></i>Your message was sent, thank you!<br />
				   </div>
           </div>


            <aside className="four columns footer-widgets">
               <div className="widget widget_contact">

					   <h4>Address and Email</h4>
					   <p className="address">
						   {name}<br />
						   {/* {state}, {country} <br /> */}
						   <span>{email}</span>
					   </p>
				   </div>

               <div className="widget widget_tweets">
                  <h4 className="widget-title">My Goal</h4>
                  <ul id="twitter">
                     <li>
                        <span>
                        My goal is to become a successful person and make my family proud of me.
                        </span>
                        {/* <b><a href="#">2 Days Ago</a></b> */}
                     </li>
                  </ul>
		         </div>
            </aside>
      </div>
   </section>
    );
  }
}

export default Contact;



